.services__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
}
.service{
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    transition: var(--transition);
    border: 1px solid var(--color-primary);
    height: fit-content;
}

.service:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head{
    
    background: var(--color-primary);
    padding: 2rem;
    border-radius:0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}

.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list{
    padding: 2rem;
}

.service__list li{
    display: flex;
    align-items: center;
    gap: 1rem;

    
}

.service__list-icon{

    color: var(--color-primary);
    font-size: 1.5rem;
    margin-right: 1rem;

   
}

.service__list p{
    font-size: 0.9rem;
}

/* ************* MEDIA QUERIES (MEDIUM DEVICES) ************* */

@media screen and (max-width:1024px){
    .services__container{
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }

    .service{
        border-radius: 0 0 2rem 2rem;
        transition: var(--transition);
        border: 1px solid var(--color-primary);
        height: fit-content;
    }
    
}

    
/********************* MEDIA QUERIES (EXTRA SMALL DEVICES) *********************/

@media screen and (max-width: 600px){
    .services__container{
         width: 65%;
        margin: 0 auto 3rem;
        
    }

    .service{
        border-radius: 0 0 2rem 2rem;
        transition: var(--transition);
        border: 1px solid var(--color-primary);
        height: fit-content;
    }
    
}

